@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
#root, body, .App {
    margin: 0px;
    padding: 0px;

    font-family: 'Source Sans Pro', sans-serif;
}

a {
    text-decoration: none;
    color: white;
}

.App-header {
    display: flex;
    /* justify-content: center; */
    align-items: center;

    background-color: rgba( 3,37,65);
    height: 64px;
    width: 100%;
}
.App-header-text {
    margin: 30px;

    font-size: 2.5rem;
    color: #fff;
    font-weight: 600;
}

footer {    
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;

    background-color: rgba( 3,37,65);
    height: 100%;
    width: 100%;
    align-content: center;

    font-size: 1.2rem;
}

footer .foote-box {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;

    margin: 30px;

    color: white;
}

/* animation - loading list */
.isFetching {
    width: 100px;
    height: 100px;
    border-radius: 100%;
    margin: 10vh auto;
    border: 2px dashed red;

    -webkit-animation-name: seach-fetching;

            animation-name: seach-fetching;
    -webkit-animation-duration: .5s;
            animation-duration: .5s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}

@-webkit-keyframes seach-fetching {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg) scale(1.3);
                transform: rotate(360deg) scale(1.3);
    }
}

@keyframes seach-fetching {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg) scale(1.3);
                transform: rotate(360deg) scale(1.3);
    }
}
.main {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    font-size: 1rem;

}
.movie-item-wrapper {
    display: flex;

    border-radius: 10px;
    margin: 30px 0px 30px 30px;
}
.movie-item-wrapper-a {
    display: flex;
    flex-direction: column;

    width: 250px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px  5px rgba(34, 60, 80, 0.2);
}
.movie-item-wrapper-a img {
    border-radius: 10px 10px 0px 0px;
}
.movie-wrapper-info {
    color: black;
    margin-left: 30px;
}

.movie-wrapper-info-name {
    font-size: 1.3rem;
    font-weight: bold;
}
.movie-info {
    display: flex;

    color: white;

    padding: 30px;
}

.back {
    padding: 30px 30px 10px 30px;
}
.back-to-main {
    padding-bottom: 5px;

    border-bottom: 2px solid red;

}

.movie-info-img {
    min-width: 300px;
    width: 300px;
    height: 450px;
}

.movie-info-about {
    margin-left: 50px;
    font-size: 1.5rem;
}
.movie-info-about-title {
    font-weight: 700;
    font-size: 3.2rem;
    margin: 0rem;
}

.movie-info-about-relise span:not(:nth-last-child(1))::after{
    content: '\2022';
    font-size: 1.1em;
    margin: 10px;
}

.movie-info-about-tagline {
    font-weight: 400;
    font-style: italic;
    opacity: 0.7;
}

.movie-info-about-production_companies {
    margin-top: 20px;
}

.movie-info-about-production_companies-name:not(:nth-last-child(1))::after{
    content: ', ';
    font-size: 1.1em;
}
.movie-info-about-production_companies-name:nth-last-child(1)::after{
    content: ';';
    font-size: 1.1em;
}

.movie-info-about-overview, .movie-info-about-production_companies-name {
    font-size: 1.2rem;
}

